

/* eslint-disable no-unused-vars */


import React, { Component } from "react"
import MetaTags from 'react-meta-tags';
import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  CardHeader,
  CardTitle,
  FormGroup,
  Label,
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
} from "reactstrap"

import {
  CButton,
  CCard,
  CCardHeader,
  CCardBody,
  CCardFooter,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import Select from "react-select"
import axios from 'axios'
import  { Redirect } from 'react-router-dom'

import { French } from "flatpickr/dist/l10n/fr.js";

// import Flatpickr from "react-flatpickr"
import Flatpickr from "flatpickr";

import DatePicker from "react-datepicker";
import { registerLocale, setDefaultLocale } from  "react-datepicker";
import es from 'date-fns/locale/es';
import $ from 'jquery';
import conf from '../../../conf.js';
import ReactSelect from 'react-select'
import makeAnimated from 'react-select/animated';
import ville from "../../dict/ville.js";
import { storage } from "../../../firebase/firebase";
import ModalMessage from "../../pages/messages/ModalMessage"
const animatedComponents = makeAnimated();


// const cities = require('all-the-cities');

registerLocale('es', es)



class ModifierMatch extends Component {
    constructor(props) {
        super(props)
        this.state = {
          ok:"",
          equipe:[],
          tournoi:{},
          equipeA:{},
          equipeB:{},
          niveau:"",
          etat:"",
          terrain:{},
          terrains:[],
          holder_niveau:"",
          holder_etat:"",
          holder_visite:"",
          holder_visiteur:"",
          id:0,
          status:false,
          heure:"",
          date:""
        }
        this.handleChangeNiveau.bind(this);
        this.handleChangeEquipeA.bind(this);
        this.handleChangeEquipeB.bind(this);
        this.handleChangeEtat.bind(this);
        this.handleChangeTerrain.bind(this);
    }

    componentDidMount() {
      let  data=  JSON.parse(localStorage.getItem('token_auth'))
      var id=localStorage.getItem("idMatch")
      this.setState({id})
      var id_tournoi=localStorage.getItem("idTournoi")
      let equipe=[]
      const requestOptions = {
        method: 'GET',
        headers: { 
          'Authorization':'Bearer '+data.token,
    
        },
      };
  
      fetch(conf.url+"match/matchs/"+id,requestOptions)
      .then((response) => response.json())
      .then((data) => {
        console.log("data match",data);
        $("#codevideo").val(data.codeVideoEmded);
        $("#scorevisiteur").val(data.scoreVisiteur);
        $("#scorevisite").val(data.scoreVisite);
        this.setState({niveau:data.niveauTournoi,
          holder_niveau:data.niveauTournoi,
          holder_visite:data.equipeVisites[0].nomEquipe,
          equipeA:data.equipeVisites[0],
          equipeB:data.equipeVisiteurs[0],
          holder_visiteur:data.equipeVisiteurs[0].nomEquipe,
          etat:data.repousse,
          heure:data.heure,
          date:data.date,
          holder_etat:data.repousse})
        })

        fetch(conf.url+"terrain/terrains",requestOptions)
        .then((response) => response.json())
        .then((data) => {
          console.log("data",data);
         
          data.map((element,idx)=>{
          this.setState({terrains:[...this.state.terrains,{label:element.nom,value:element}]})
          })
        
          })

          fetch(conf.url+"tournoi/tournois/"+id_tournoi,requestOptions)
          .then((response) => response.json())
          .then((data) => {
            console.log("data",data);
            $("#nom_tournoi").val(data.nom)
            this.setState({tournoi:data})
            data.equipes.map((element,idx)=>{
              this.setState({equipe:[...this.state.equipe,{label:element.nomEquipe,value:element}]})
              })
            })


    }

    handleUpdateMatch=()=>{

      let  token=  JSON.parse(localStorage.getItem('token_auth'))
      var data= {
        "id":this.state.id,
        "scoreVisiteur": $("#scorevisiteur").val(),
        "scoreVisite": $("#scorevisite").val(),
        "niveauTournoi":this.state.niveau,
        "codeVideoEmded":$("#codevideo").val(),
        "equipeVisites": [this.state.equipeA],
        "equipeVisiteurs": [this.state.equipeB],
        "repousse":this.state.etat,
        "terrain":this.state.terrain,
        "tournoi":this.state.tournoi,
        "matchDejaJoue":this.state.status,
        "date":this.state.date,
        "heure":this.state.heure
  
      }
  
      console.log("data a transmettre",data)
  
      const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json','Accept': 'application/json',
        'Authorization':'Bearer '+token.token,
      },
        body: JSON.stringify(data)
    };
  
    fetch(conf.url+"match/update",requestOptions)
    .then((response) => response.json())
    .then((data) => {
       console.log("okkk====>",data)
       this.setState({ok: <Redirect to='/detail-tournoi'/>});
    
     }
     
    );
      
    } 
  
  
     handleChangeEquipeA = (e) => {
      console.log("adate =====>",e)
      try{
        this.setState({equipeA:e.value})
       
      }catch(ee){
  
      }
    }
      handleChangeEquipeB = (e) => {
        console.log("adate =====>",e)
        try{
          this.setState({equipeB:e.value})
         
        }catch(ee){
    
        }
      }
  
      handleChangeNiveau = (e) => {
        console.log("adate =====>",e)
        try{
          this.setState({niveau:e.value})
         
        }catch(ee){
    
        }
      }
  
      handleChangeEtat = (e) => {
        console.log("adate =====>",e)
        try{
          this.setState({etat:e.value})
          if(e.value=="JOUE"){
            this.setState({status:true})
          }
         
        }catch(ee){
    
        }
      }
  
      handleChangeTerrain= (e) => {
        console.log("adate =====>",e)
        try{
          this.setState({terrain:e.value})
         
        }catch(ee){
    
        }
      }

    handleChangeDate= () => {
     var data =$("#date_match").val()
      try{
        this.setState({date:data.split("-")[2]+"/"+data.split("-")[1]+"/"+data.split("-")[0]})
       
      }catch(ee){
  
      }
    }
    handleChangeHeure= () => {
     
      try{
        this.setState({heure:$("#heure_match").val()})
       
      }catch(ee){
  
      }
    }

    handleRetour=()=>{
      this.setState({ok:<Redirect to='/detail-tournoi'/>})
    }

   
  render() {

    const { pick } = this.state
    const { selectedGroup } = this.state
    const { selectedLangue } = this.state
    const animatedComponents = makeAnimated();

    const niveau = [
     
      { label: "16ième", value: "16ième" },
      { label: "8ème", value: "8ème" },
      { label: "4ième", value: "4ième" },
      { label: "2ième", value: "2ième" },
      { label: "Finale", value: "Finale" },
    
     ]

  
     const optionEtat = [
      {
        options: [
          { label: "Joué", value: "JOUE" },
          { label: "Programmé", value: "PROGRAMME" },
          { label: "Annulé", value: "ANNULE" },
          { label: "Repoussé", value: "REPOUSE" },
        ]
      },  
    ]
    

    const optionNiveauTournoi = [
      {
        options: [
          { label: "NONE ", value: "none" },
        ]
      },
    ]
    


  return (
        <>
          <React.Fragment>
          <div className="page-content">
             {this.state.ok}

            <Container fluid={false} className="justify-content-center">

              <Row className="justify-content-center">
              <Col md="12" lg="12" xl="12">

                {/* <Row ClassName="mb-3">
                  <Col md="8">
                      <div className="page-title-box d-flex align-items-center justify-content-between">
                        <h4 className="mb-0 font-size-18">Nouveau compte</h4>
                      </div>
                  </Col>
                </Row> */}
     
                

                <Card ClassName="mt-5">
                  {/* <CCardHeader className="p-4"> */}
                    <h1 className="p-4" >Enregistré un  match</h1>
                  {/* </CCardHeader> */}


                  <CardBody>
                    <Row> 
                      <Col md="5" className="mt-2">
                        <label>Equipe à domicile</label>
                        <ReactSelect
                          closeMenuOnSelect={true}
                          components={animatedComponents}
                          onChange={this.handleChangeEquipeA}
                          options={this.state.equipe}
                          placeholder={this.state.holder_visite}
                        />
                      </Col>
                      <Col md="1" className="mt-2">
                        <label>Score</label>
                        <Input
                          className="form-control"
                          type="number"
                          min="0"
                          id="scorevisite"
                          placeholder="00"
                        />
                      </Col>
                      <Col md="5" className="mt-2">
                        <label>Equipe visiteur</label>
                        <ReactSelect
                          closeMenuOnSelect={true}
                          components={animatedComponents}
                          onChange={this.handleChangeEquipeB}
                          options={this.state.equipe}
                          placeholder={this.state.holder_visiteur}
                        />
                      </Col>
                      <Col md="1" className="mt-2">
                        <label>Score</label>
                        <Input
                          className="form-control"
                          type="number"
                          min="0"
                          id="scorevisiteur"
                          placeholder="00"
                        />
                      </Col>
                    </Row>
                    <Row className="mt-4">
                    <Col md="5" className="mt-2">
                        <label>Nom du tournoi</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="nom_tournoi"
                          readOnly
                        />
                      </Col>
                      <Col md="6" className="mt-2">
                        <label>Niveau du tournoi</label>
                        <ReactSelect
                          closeMenuOnSelect={true}
                          components={animatedComponents}
                          onChange={this.handleChangeNiveau}
                          options={niveau}
                          placeholder={this.state.holder_niveau}
                        />
                      </Col>
                      </Row>
                    <Row className="mt-4">
                      <Col md="4" className="mt-2">
                        <label>Nom du terrain</label>
                        <ReactSelect
                          closeMenuOnSelect={true}
                          components={animatedComponents}
                          onChange={this.handleChangeTerrain}
                          options={this.state.terrains}
                          placeholder="Nom du terrain"
                        />
                      </Col>

                      <Col md="4" className="mt-2">
                        <label>Code vidéo</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="codevideo"
                          placeholder="Code vidéo"
                        />
                      </Col>

                      <Col md="4" className="mt-2">
                        <label>Etat</label>
                        <ReactSelect
                          closeMenuOnSelect={true}
                          components={animatedComponents}
                          onChange={this.handleChangeEtat}
                          options={optionEtat}
                          placeholder={this.state.holder_etat}
                        />
                      </Col>
                    </Row>

                    <Row className="mt-4">
                      <Col md="6" className="mt-2">
                        <label>Date du match</label>
                        <Input
                         
                          className="form-control"
                          type="date"
                          id="date_match"
                          placeholder="Date du match"
                          onChange={this.handleChangeDate}
                        />
                        <span>{this.state.date}</span>
                      </Col>
                      <Col md="6" className="mt-2">
                        <label>Heure du match</label>
                        <Input
                          className="form-control"
                          type="time"
                          id="heure_match"
                          placeholder="Heure du match"
                          onChange={this.handleChangeHeure}
                        />
                        <span>{this.state.heure}</span>
                      </Col>
                    </Row>
                   
                    
                    <Row className="justify-content-start mt-5">
                     
                      <Col md="6" className="mt-1">
                        <Button onClick={this.handleRetour} color="danger" block className="b-lg">
                          Annuler
                        </Button>
                      </Col>
                      <Col md="6" className="mt-1">
                        <Button id="initial"  onClick={this.handleUpdateMatch} color="success" block className="b-lg" >
                          Modifier le match
                        </Button>
                        {/* <Button id="chargement" color="info" className="b-lg" type="button" disabled>
                        <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"> </span>
                          Envoi...
                        </Button> */}
                      </Col>
                    </Row>
                  </CardBody>
                </Card>
              </Col>
            </Row>
          </Container>
        </div>
             
      </React.Fragment>
    </>
        )}

  }
  export default ModifierMatch